import React from "react";
//import { Link } from 'react-router-dom';
import MenuOutlinedIcon from "@material-ui/icons/MenuOutlined";




import UserDropdown from "./Items-Header/UserDropdown";
import { AppBar, Box, IconButton, Toolbar } from "@mui/material";
import NotificationsDropdown from "./Items-Header/NotificationsDropdown";

const Header = (props) => {

//props.sx  or  {backgroundColor: '#B84DA9',}
  return (
    <AppBar sx={props.sx} elevation={1} className={props.customClass}>
      <Toolbar >
        <IconButton
          color="info"
          aria-label="menu"
          onClick={props.toggleMobileSidebar}
          sx={{
            display: {
              lg: "none",
              xs: "inline",
            },
          }}
        >
          <MenuOutlinedIcon width="20" height="20" />
        </IconButton>

        
        <Box 
        flexGrow={1} 
        />
      <NotificationsDropdown/>

        <Box
          sx={{
            width: "1.5px",
            backgroundColor: "rgba(242,242,242)",
            height: "25px",
            ml: 1,
          }}
        ></Box>
       <UserDropdown/>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
