import { Logout, PersonAdd, Settings } from '@material-ui/icons'
import { Avatar, Box, Button, Divider, ListItemIcon, Menu, MenuItem, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom';

import userimg from "../../../../assets/images/users/3.jpg";


const UserDropdown = () => {

  // 4
  const [anchorEl4, setAnchorEl4] = React.useState(null);

  const handleClick4 = (event) => {
      setAnchorEl4(event.currentTarget);
  };

  const handleClose4 = () => {
      setAnchorEl4(null);
  };

/* // 5
  const [anchorEl5, setAnchorEl5] = React.useState(null);

  const handleClick5 = (event) => {
    setAnchorEl5(event.currentTarget);
  };

  const handleClose5 = () => {
    setAnchorEl5(null);
  };*/

  return (
    <div>
        <Button
          aria-label="menu"
          color="inherit"
          aria-controls="profile-menu"
          aria-haspopup="true"
          onClick={handleClick4}
        >
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
            }}
          >
            <Avatar
              src={userimg}
              alt={userimg}
              sx={{
                width: "45px",
                height: "45px",
              }}
            />
          </Box>
        </Button>
        <Menu
          id="profile-menu"
          anchorEl={anchorEl4}
          keepMounted
          open={Boolean(anchorEl4)}
          onClose={handleClose4}
          anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
          transformOrigin={{ horizontal: "right", vertical: "top" }}
          sx={{
            "& .MuiMenu-paper": {
              width: "250px",
              right: 0,
              top: "70px !important",
            },
          }}
        >
          <MenuItem onClick={handleClose4}>
            <Avatar
              src={userimg}
              alt={userimg}
              sx={{
                width: "45px",
                height: "45px",
              }}
            />
          <Typography noWrap sx={{ marginLeft: 3, alignItems: 'flex-start', flexDirection: 'column' }}>
            <Typography 
            noWrap 
            sx={{ fontWeight: 400 }} 
            component="div"
            >
              Daniel Stark Parkour TestTestTest
            </Typography>
            <Typography variant='body2' sx={{ fontSize: '0.8rem', color: 'text.disabled' }}>
              Paciente
            </Typography>
            <Typography variant='body2' sx={{ fontSize: '0.8rem', color: 'text.disabled' }}>
              CC 1045784542
            </Typography>
          </Typography>
         
          {/*            <Box
              sx={{
                ml: 2,
              }}
            >
              My account
            </Box>*/}

          </MenuItem>
          <Divider />
          <MenuItem onClick={handleClose4}>
            <ListItemIcon>
              <PersonAdd fontSize="small" />
            </ListItemIcon>
            Perfil
          </MenuItem>
          <MenuItem onClick={handleClose4}>
            <ListItemIcon>
              <Settings fontSize="small" />
            </ListItemIcon>
            Settings
          </MenuItem>
          <Link to="/login">

          <MenuItem onClick={handleClose4}>
            <ListItemIcon>
              <Logout fontSize="small" />
            </ListItemIcon>
            Logout
          
          </MenuItem>
          </Link>
        </Menu>

    </div>
  )
}

export default UserDropdown